import React from 'react';
import { formatNumberEnglish } from '@/utils/formatters';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardHeader } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { CheckCircle, Clock, XCircle, Phone, DollarSign, Calendar, Trash2, Printer, ArrowDownLeft, ArrowUpDown, Receipt, Store, User, CreditCard, ArrowRight } from 'lucide-react';

interface Transaction {
  id: string;
  type: 'send' | 'receive' | 'withdraw';
  senderPhone: string;
  receiverPhone: string;
  amount: number;
  status: 'pending' | 'completed' | 'failed';
  createdAt: Date;
  withdrawnAmount?: number; // المبلغ المسحوب
  sentAmount?: number; // المبلغ المرسل
  fromWallet?: string; // معرف المحفظة المستخدمة
  // البيانات الجديدة للإيصال المفصل
  senderNumber?: string; // الرقم المرسل
  senderName?: string; // الرقم الراسل في التحويل
  shopName?: string; // اسم المحل
  transferredAmount?: number; // المبلغ المحول
  receiverNumber?: string; // الرقم المستقبل
  withdrawnAmountDetailed?: number; // المبلغ المسحوب المفصل
  merchantShopName?: string; // اسم المحل الذي أدخله المستخدم عند إنشاء الحساب
  transactionReference?: string; // مرجع المعاملة
  commission?: number; // العمولة
  fee?: number; // الرسوم الإضافية للتحويل
  notes?: string; // ملاحظات إضافية
}

interface TransactionDetailsModalProps {
  isOpen: boolean;
  onClose: () => void;
  transaction: Transaction | null;
  onDelete: (transactionId: string) => void;
}

const TransactionDetailsModal: React.FC<TransactionDetailsModalProps> = ({
  isOpen,
  onClose,
  transaction,
  onDelete
}) => {
  if (!transaction) return null;

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'completed':
        return <CheckCircle className="h-5 w-5 text-green-500" />;
      case 'pending':
        return <Clock className="h-5 w-5 text-yellow-500" />;
      case 'failed':
        return <XCircle className="h-5 w-5 text-red-500" />;
      default:
        return <Clock className="h-5 w-5 text-gray-500" />;
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'completed':
        return 'مكتملة';
      case 'pending':
        return 'قيد الانتظار';
      case 'failed':
        return 'فاشلة';
      default:
        return 'غير معروف';
    }
  };

  const getTransactionTypeText = (type: string) => {
    switch (type) {
      case 'withdraw':
        return 'سحب';
      case 'send':
        return 'إرسال';
      case 'receive':
        return 'استقبال';
      default:
        return 'تحويل';
    }
  };

  const getTransactionIcon = (type: string) => {
    switch (type) {
      case 'withdraw':
      case 'machine_withdrawal':
        return <ArrowDownLeft className="h-5 w-5 text-green-500" />;
      default:
        return <ArrowUpDown className="h-5 w-5 text-blue-500" />;
    }
  };

  const handlePrint = () => {
    const hasSenderNumber = !!transaction.senderNumber;
    const hasSenderName = !!transaction.senderName;
    const senderLabel = hasSenderNumber
      ? 'الرقم المرسل:'
      : hasSenderName
      ? 'الرقم الراسل:'
      : 'رقم المحفظة:';
    const senderValue = hasSenderNumber
      ? transaction.senderNumber
      : hasSenderName
      ? transaction.senderName
      : transaction.senderPhone;

    const printContent = `
      <div style="font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; max-width: 500px; margin: 0 auto; padding: 30px; background: #fff;">
        <!-- Header -->
        <div style="text-align: center; margin-bottom: 30px; border-bottom: 2px solid #2563eb; padding-bottom: 20px;">
          <h1 style="color: #2563eb; margin-bottom: 5px; font-size: 28px;">إيصال المعاملة</h1>
          <p style="color: #666; font-size: 16px; margin: 0;">رقم المرجع: ${transaction.transactionReference || transaction.id}</p>
          <p style="color: #888; font-size: 14px; margin: 5px 0 0 0;">${new Date().toLocaleString('ar-EG')}</p>
        </div>
        
        <!-- Shop Info -->
        <div style="background: #f8fafc; border-radius: 12px; padding: 20px; margin-bottom: 20px;">
          <h3 style="color: #1e293b; margin: 0 0 15px 0; font-size: 18px;">معلومات المحل</h3>
          <div style="display: flex; justify-content: space-between; margin-bottom: 8px;">
            <span style="font-weight: 600; color: #475569;">اسم المحل:</span>
            <span style="color: #1e293b;">${transaction.merchantShopName || transaction.shopName || 'غير محدد'}</span>
          </div>
        </div>
        
        <!-- Transaction Details -->
        <div style="background: #fff; border: 2px solid #e2e8f0; border-radius: 12px; padding: 20px; margin-bottom: 20px;">
          <h3 style="color: #1e293b; margin: 0 0 15px 0; font-size: 18px;">تفاصيل المعاملة</h3>
          
          <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
            <span style="font-weight: 600; color: #475569;">نوع المعاملة:</span>
            <span style="color: #2563eb; font-weight: 600;">${getTransactionTypeText(transaction.type)}</span>
          </div>
          
          <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
            <span style="font-weight: 600; color: #475569;">الحالة:</span>
            <span style="color: ${transaction.status === 'completed' ? '#16a34a' : transaction.status === 'pending' ? '#ca8a04' : '#dc2626'}; font-weight: 600;">
              ${getStatusText(transaction.status)}
            </span>
          </div>

          ${(transaction as any)?.cashierName ? `
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">الكاشير:</span>
              <span style="color: #1e293b;">${(transaction as any).cashierName}</span>
            </div>
          ` : ''}
          
          ${transaction.type !== 'withdraw' ? `
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">${senderLabel}</span>
              <span style="color: #1e293b;">${senderValue}</span>
            </div>
            
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">الرقم المستقبل:</span>
              <span style="color: #1e293b;">${transaction.receiverNumber || transaction.receiverPhone}</span>
            </div>
            
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">المبلغ المحول:</span>
              <span style="color: #2563eb; font-weight: bold; font-size: 16px;">${formatNumberEnglish(transaction.transferredAmount || transaction.amount)} جنيه</span>
            </div>
          ` : `
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">رقم المحفظة:</span>
              <span style="color: #1e293b;">${transaction.senderPhone}</span>
            </div>
            
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">المبلغ المسحوب:</span>
              <span style="color: #dc2626; font-weight: bold; font-size: 16px;">${formatNumberEnglish(transaction.withdrawnAmountDetailed || transaction.withdrawnAmount || transaction.amount)} جنيه</span>
            </div>
          `}
          
          ${transaction.commission ? `
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">العمولة:</span>
              <span style="color: #f59e0b; font-weight: 600;">${formatNumberEnglish(transaction.commission || 0)} جنيه</span>
            </div>
          ` : ''}
          ${transaction.commission ? `
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">الربح:</span>
              <span style="color: #16a34a; font-weight: 600;">${formatNumberEnglish(transaction.commission || 0)} جنيه</span>
            </div>
          ` : ''}
          ${((transaction as any)?.fee && Number((transaction as any).fee) > 0) ? `
            <div style="display: flex; justify-content: space-between; margin-bottom: 12px; padding: 8px 0; border-bottom: 1px solid #f1f5f9;">
              <span style="font-weight: 600; color: #475569;">الرسوم:</span>
              <span style="color: #64748b; font-weight: 600;">${formatNumberEnglish((transaction as any).fee || 0)} جنيه</span>
            </div>
          ` : ''}
          
          <div style="display: flex; justify-content: space-between; padding: 8px 0;">
            <span style="font-weight: 600; color: #475569;">التاريخ والوقت:</span>
            <span style="color: #1e293b;">${transaction.createdAt.toLocaleString('ar-EG')}</span>
          </div>
        </div>
        
        <!-- Total Amount -->
        <div style="background: linear-gradient(135deg, #2563eb 0%, #1d4ed8 100%); border-radius: 12px; padding: 20px; margin-bottom: 20px; text-align: center;">
          <h3 style="color: white; margin: 0 0 10px 0; font-size: 16px;">إجمالي المبلغ</h3>
          <p style="color: white; font-size: 24px; font-weight: bold; margin: 0;">
            ${transaction.type === 'withdraw' ? '-' : ''}${formatNumberEnglish(transaction.amount)} جنيه
          </p>
        </div>
        
        
        
        
        <!-- Footer -->
        <div style="text-align: center; color: #64748b; font-size: 12px; margin-top: 30px; padding-top: 20px; border-top: 1px solid #e2e8f0;">
          <p style="margin: 0 0 5px 0;">شكراً لاستخدام خدماتنا</p>
          <p style="margin: 0;">تم إنشاء الإيصال في: ${new Date().toLocaleString('ar-EG')}</p>
        </div>
      </div>
    `;

    const printWindow = window.open('', '_blank');
    if (printWindow) {
      printWindow.document.write(`
        <html>
          <head>
            <title>إيصال المعاملة - ${transaction.transactionReference || transaction.id}</title>
            <meta charset="UTF-8">
            <style>
              body { 
                margin: 0; 
                padding: 20px; 
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                background: #f8fafc;
              }
              @media print {
                body { 
                  margin: 0; 
                  background: white;
                }
              }
            </style>
          </head>
          <body>
            ${printContent}
          </body>
        </html>
      `);
      printWindow.document.close();
      printWindow.print();
    }
  };

  const handleDelete = () => {
    if (window.confirm('هل أنت متأكد من حذف هذه المعاملة؟ لا يمكن التراجع عن هذا الإجراء.')) {
      onDelete(transaction.id);
      onClose(); // إغلاق النافذة بعد الحذف
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="w-[92vw] sm:w-auto sm:max-w-2xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-xl">
            <Receipt className="h-6 w-6 text-blue-600" />
            إيصال المعاملة المفصل
          </DialogTitle>
        </DialogHeader>

        <div className="space-y-6">
          {/* Header Section */}
          <Card className="bg-gradient-to-r from-blue-50 to-indigo-50 border-blue-200">
            <CardHeader className="text-center pb-2">
              <div className="flex items-center justify-center gap-2 mb-2">
                {getStatusIcon(transaction.status)}
                <Badge 
                  variant={transaction.status === 'completed' ? 'default' : 'secondary'}
                  className="text-sm"
                >
                  {getStatusText(transaction.status)}
                </Badge>
              </div>
              <h3 className="text-2xl font-bold text-blue-600">
                {transaction.type === 'withdraw' ? '-' : ''}{formatNumberEnglish(transaction.amount)} جنيه
              </h3>
              <p className="text-sm text-gray-600">
                رقم المرجع: {transaction.transactionReference || transaction.id}
              </p>
            </CardHeader>
          </Card>

          {/* Shop Information */}
          <Card>
            <CardHeader className="pb-3">
              <div className="flex items-center gap-2">
                <Store className="h-5 w-5 text-green-600" />
                <h4 className="font-semibold text-lg">معلومات المحل</h4>
              </div>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                <span className="font-medium text-gray-700">اسم المحل:</span>
                <span className="text-gray-900">{transaction.merchantShopName || transaction.shopName || 'غير محدد'}</span>
              </div>
              {(transaction as any)?.cashierName && (
                <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                  <span className="font-medium text-gray-700">الكاشير:</span>
                  <span className="text-gray-900">{(transaction as any).cashierName}</span>
                </div>
              )}
            </CardContent>
          </Card>

          {/* Transaction Details */}
          <Card>
            <CardHeader className="pb-3">
              <div className="flex items-center gap-2">
                <CreditCard className="h-5 w-5 text-blue-600" />
                <h4 className="font-semibold text-lg">تفاصيل المعاملة</h4>
              </div>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="grid grid-cols-1 gap-3">
                <div className="flex items-center justify-between p-3 bg-blue-50 rounded-lg">
                  <span className="font-medium text-blue-700">نوع المعاملة:</span>
                  <Badge variant="outline" className="bg-blue-100 text-blue-800">
                    {getTransactionTypeText(transaction.type)}
                  </Badge>
                </div>

                {transaction.type !== 'withdraw' ? (
                  <>
                    {(() => {
                      const hasSenderNumber = !!transaction.senderNumber;
                      const hasSenderName = !!transaction.senderName;
                      const senderLabel = hasSenderNumber
                        ? 'الرقم المرسل:'
                        : hasSenderName
                        ? 'الرقم الراسل:'
                        : 'رقم المحفظة:';
                      const senderValue = hasSenderNumber
                        ? transaction.senderNumber
                        : hasSenderName
                        ? transaction.senderName
                        : transaction.senderPhone;
                      const SenderIcon = hasSenderNumber ? User : Phone;
                      return (
                        <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                          <div className="flex items-center gap-2">
                            <SenderIcon className="h-4 w-4 text-gray-500" />
                            <span className="font-medium text-gray-700">{senderLabel}</span>
                          </div>
                          <span className="text-gray-900 font-mono">{senderValue}</span>
                        </div>
                      );
                    })()}

                    <div className="flex items-center justify-center p-2">
                      <ArrowRight className="h-6 w-6 text-blue-500" />
                    </div>

                    <div className="flex items-center justify-between p-3 bg-green-50 rounded-lg">
                      <div className="flex items-center gap-2">
                        <User className="h-4 w-4 text-green-500" />
                        <span className="font-medium text-green-700">الرقم المستقبل:</span>
                      </div>
                      <span className="text-green-900 font-mono">{transaction.receiverNumber || transaction.receiverPhone}</span>
                    </div>

                    <div className="flex items-center justify-between p-3 bg-blue-50 rounded-lg">
                      <div className="flex items-center gap-2">
                        <DollarSign className="h-4 w-4 text-blue-500" />
                        <span className="font-medium text-blue-700">المبلغ المحول:</span>
                      </div>
                      <span className="text-blue-900 font-bold text-lg">{formatNumberEnglish(transaction.transferredAmount || transaction.amount)} جنيه</span>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                      <div className="flex items-center gap-2">
                        <Phone className="h-4 w-4 text-gray-500" />
                        <span className="font-medium text-gray-700">رقم المحفظة:</span>
                      </div>
                      <span className="text-gray-900 font-mono">{transaction.senderPhone}</span>
                    </div>

                    {(transaction as any)?.senderName && (
                      <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                        <div className="flex items-center gap-2">
                          <User className="h-4 w-4 text-gray-500" />
                          <span className="font-medium text-gray-700">اسم صاحب المحفظة:</span>
                        </div>
                        <span className="text-gray-900">{(transaction as any).senderName}</span>
                      </div>
                    )}

                    <div className="flex items-center justify-between p-3 bg-red-50 rounded-lg">
                      <div className="flex items-center gap-2">
                        <DollarSign className="h-4 w-4 text-red-500" />
                        <span className="font-medium text-red-700">المبلغ المسحوب:</span>
                      </div>
                      <span className="text-red-900 font-bold text-lg">{formatNumberEnglish(transaction.withdrawnAmountDetailed || transaction.withdrawnAmount || transaction.amount)} جنيه</span>
                    </div>
                  </>
                )}

                {transaction.commission && (
                  <div className="flex items-center justify-between p-3 bg-yellow-50 rounded-lg">
                    <span className="font-medium text-yellow-700">العمولة:</span>
                    <span className="text-yellow-900 font-semibold">{transaction.commission} جنيه</span>
                  </div>
                )}

                {transaction.commission && (
                  <div className="flex items-center justify-between p-3 bg-green-50 rounded-lg">
                    <span className="font-medium text-green-700">الربح:</span>
                    <span className="text-green-900 font-semibold">{transaction.commission} جنيه</span>
                  </div>
                )}

                {(transaction as any)?.fee && Number((transaction as any).fee) > 0 && (
                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <span className="font-medium text-gray-700">الرسوم:</span>
                    <span className="text-gray-900 font-semibold">{(transaction as any).fee} جنيه</span>
                  </div>
                )}

                <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                  <div className="flex items-center gap-2">
                    <Calendar className="h-4 w-4 text-gray-500" />
                    <span className="font-medium text-gray-700">التاريخ والوقت:</span>
                  </div>
                  <span className="text-gray-900">{transaction.createdAt.toLocaleString('ar-EG')}</span>
                </div>
              </div>
            </CardContent>
          </Card>

          

          {/* Action Buttons */}
          <div className="flex gap-3">
            <Button
              onClick={handlePrint}
              className="flex-1 bg-blue-600 hover:bg-blue-700 text-white"
              size="lg"
            >
              <Printer className="h-5 w-5 mr-2" />
              طباعة الإيصال
            </Button>
            
            <Button
              onClick={handleDelete}
              variant="destructive"
              className="flex-1"
              size="lg"
            >
              <Trash2 className="h-5 w-5 mr-2" />
              حذف المعاملة
            </Button>
          </div>

          <Button
            onClick={onClose}
            variant="outline"
            className="w-full"
            size="lg"
          >
            إغلاق الإيصال
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default TransactionDetailsModal;
